import { blogData } from '@/lib/blog-data';
import Navigation from '@/app/components/Navigation';
import { Button } from '@/components/ui/button';
import { ArrowLeft, Calendar, Clock, User, ArrowRight } from 'lucide-react';
import Link from 'next/link';
import { Card, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';

export function generateStaticParams() {
  return Object.keys(blogData).map((slug) => ({ slug }));
}

export default async function BlogPostPage({ params }: { params: Promise<{ slug: string }> }) {
  const { slug } = await params;
  const post = blogData[slug];

  if (!post) return <div className="p-20 text-center">Post not found.</div>;

  return (
    <div className="min-h-screen bg-white dark:bg-gray-950">
      <Navigation />
      <article className="pt-32 pb-20 px-4">
        <div className="max-w-4xl mx-auto">
          <Link href="/blog" className="flex items-center text-blue-600 mb-8 hover:underline">
            <ArrowLeft className="mr-2" size={16} /> Back to Blog
          </Link>
          <span className="bg-blue-100 text-blue-700 px-3 py-1 rounded-full text-xs font-bold uppercase">{post.category}</span>
          <h1 className="text-4xl md:text-6xl font-bold mt-6 mb-8 dark:text-white">{post.title}</h1>
          <div className="flex gap-6 text-gray-400 mb-10 border-b pb-8">
            <div className="flex items-center gap-2"><Calendar size={18}/> {post.date}</div>
            <div className="flex items-center gap-2"><Clock size={18}/> {post.readTime}</div>
            <div className="flex items-center gap-2"><User size={18}/> By {post.author}</div>
          </div>
          <img src={post.image} alt={post.title} className="w-full rounded-3xl mb-12 shadow-xl" />
          <div className="prose prose-lg dark:prose-invert max-w-none dark:text-gray-300">
            {post.content}
          </div>
          
          {/* Related Posts */}
          <div className="mt-20 border-t pt-20">
            <h2 className="text-3xl font-bold mb-10 dark:text-white">Recommended Reading</h2>
            <div className="grid md:grid-cols-2 gap-8">
              {post.relatedPosts.map(rel => (
                <Link key={rel.slug} href={`/blog/${rel.slug}`}>
                  <Card className="h-full hover:border-blue-500 transition-all dark:bg-gray-900">
                    <CardHeader>
                      <CardTitle className="dark:text-white">{rel.title}</CardTitle>
                      <CardDescription>{rel.excerpt}</CardDescription>
                    </CardHeader>
                  </Card>
                </Link>
              ))}
            </div>
          </div>
        </div>
      </article>
    </div>
  );
}